.product_title {
    width: 500px
}

.product_description {
    float: left;
    padding: 10px 20px 0 19px;
    width: 500px;
    font-size: 16px;
    line-height: 24px;
    margin: 0;
    display: block;
    min-height: 60px;
    font-weight: 700;
}
.product__link {
    text-decoration: none;
    color: #000
}

.product__link:hover {
    border-bottom: 1px solid black;
}

.product_tags {
    display: block;
    float: left;
    padding: 0 0 10px 20px;
    width: 505px;
    font-size: 16px;
    line-height: 17px;
}

.product_tags p {
    display: inline-block;
    font-size: 13px;
    line-height: 16px;
    color: #545454;
    font-weight: 700;
    margin: 0;
}

.product_tags a {
    display: inline;
    font-size: 13px;
    line-height: 16px;
    color: #666;
    text-decoration: none;
}
.product_tags a:hover {
    border-bottom: 1px solid grey;
}

@media (max-width: 1249px) and (min-width: 1000px) {

.product_title {
    width: 330px;
    padding-left: 10px;
}

.product_description,
.product_tags {
    width: 330px;
    padding-left: 0px
    }
}


@media (max-width: 999px) and (min-width: 0px) {

    .product_title {
        width: auto;
    }

    .product_description {
        width: auto;
        font-size: 14px;
        line-height: 20px;
        padding: 0px;
    }

    .product_tags {
        display: none;
    }
}